import { Component } from "@angular/core";
import { ActivatedRoute, Router, Event, NavigationEnd } from "@angular/router";
import { NavController } from "@ionic/angular";
import { SmartAudioProvider } from "../services/smart-audio";
import { DataProvider } from "../services/data";

import { PlayerRoles } from "../../assets/Constants";

@Component({
  selector: "page-tabs",
  templateUrl: "tabs.html"
})
export class TabsPage {
  tabs = [
    {
      text: "پیام‌ها",
      id: "inboxTab",
      src: "../../assets/imgs/tab/inbox.png",
      activeSrc: "../../assets/imgs/tab/inbox-active.png",
      route: "inbox"
    },
    {
      text: "گروهی",
      id: "clanTab",
      src: "../../assets/imgs/tab/clan.png",
      activeSrc: "../../assets/imgs/tab/clan-active.png",
      route: "clan"
    },
    {
      text: "خانه",
      id: "homeTab",
      src: "../../assets/imgs/tab/home.png",
      activeSrc: "../../assets/imgs/tab/home-active.png",
      route: "home"
    },
    {
      text: "تمرینی",
      id: "trainingTab",
      src: "../../assets/imgs/tab/training.png",
      activeSrc: "../../assets/imgs/tab/training-active.png",
      route: "training"
    },
    {
      text: "برترین‌ها",
      id: "trainingTab",
      src: "../../assets/imgs/tab/leaderboard.png",
      activeSrc: "../../assets/imgs/tab/leaderboard-active.png",
      route: "leaderboard"
    }
  ];

  activeRoute: string = "home";

  badge: any;

  readyToPlay: any;
  isShowingAlert = false;

  player: any;

  playerRoles = PlayerRoles;

  constructor(
    public navCtrl: NavController,
    private dataProvider: DataProvider,
    private smartAudio: SmartAudioProvider,
    private route: ActivatedRoute,
    private router: Router
  ) {
    this.dataProvider.isShowingAlert.subscribe((state) => {
      this.isShowingAlert = state;
    });
    this.router.events.subscribe((event: Event) => {
      if (event instanceof NavigationEnd) {
        this.activeRoute = event.urlAfterRedirects.split("/")[2];
      }
    });

    this.route.queryParams.subscribe((params) => {
      const state = this.router.getCurrentNavigation().extras.state;

      if (state) {
        if (state.islandIndex !== undefined) {
          this.dataProvider.setIslandIndex(state.islandIndex);
          setTimeout(() => this.slideToIndex("tabs/home"), 150);
        }
      }
    });

    this.readyToPlay = this.dataProvider.startGame;
    this.player = this.dataProvider.getPlayer();
    this.badge = this.dataProvider.getBadges();
  }

  slideToIndex(route: string) {
    this.router.navigate([route]);
  }

  // when any event change current tab this function will update active tab in store
  onTabSelect(e) {
    this.smartAudio.play("change");
  }

  dismiss() {
    window["backButton"]();
  }
}
